const pipeline = (x) => x();

const importers = [
  async (path) => {
    if (!/^\.[\\/](?:design-systems[\\/](?!\.)(?=.)[^\\/]*?[\\/]components(?:[\\/](?!\.)(?:(?:(?!(?:^|[\\/])\.).)*?)[\\/]|[\\/]|$)(?!\.)(?=.)[^\\/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(17);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?!.*node_modules)(?:[\\/]design-systems[\\/](?!\.)(?=.)[^\\/]*?[\\/]components(?:[\\/](?!\.)(?:(?:(?!(?:^|[\\/])\.).)*?)[\\/]|[\\/]|$)(?!\.)(?=.)[^\\/]*?\.stories\.(js|jsx|ts|tsx|mdx))$/ */
      './design-systems/' + pathRemainder
    );
  }
  ,
  async (path) => {
    if (!/^\.[\\/](?:docs[\\/]stories(?:[\\/](?!\.)(?:(?:(?!(?:^|[\\/])\.).)*?)[\\/]|[\\/]|$)(?!\.)(?=.)[^\\/]*?\.mdx)$/.exec(path)) {
      return;
    }
  
    const pathRemainder = path.substring(15);
    return import(
      /* webpackChunkName: "[request]" */
      /* webpackInclude: /(?!.*node_modules)(?:[\\/]docs[\\/]stories(?:[\\/](?!\.)(?:(?:(?!(?:^|[\\/])\.).)*?)[\\/]|[\\/]|$)(?!\.)(?=.)[^\\/]*?\.mdx)$/ */
      './docs/stories/' + pathRemainder
    );
  }
  
];

export async function importFn(path) {
  for (let i = 0; i < importers.length; i++) {
    const moduleExports = await pipeline(() => importers[i](path));
    if (moduleExports) {
      return moduleExports;
    }
  }
}